@mixin alertGenerate() {
  background-color: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  width: 100%;
  p {
    margin-bottom: 0;
  }
}

.salmon {
  font-size: 16px;
  color: $salmon;
  padding: 20px;
}

.alert {
  @include alertGenerate();
}

.alert-red {
  @extend .alert;
  background-color: #ffe591;
}
