.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.jumbotron {
  width: 100%;
  height: 400px;
  background-image: url(image/nintendo_jumbotron.jpg);
  background-size: 100%;
  background-position-y: 50%;
  background-repeat: no-repeat;
  color: beige;
}

.jumbotron .jumbo_container {
  padding-top: 120px;
}

.goods_container {
  margin-top: 50px;
  overflow: hidden;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
